import React, { Component, Fragment } from 'react';
import { Link } from 'react-router-dom';
import Spinner from '../layout/Spinner';
import Repos from './Repos';

class User extends Component {
    componentDidMount(){
        this.props.getUser(this.props.match.params.login);
        this.props.getRepos(this.props.match.params.login);
    }
    render() {
        const {name,
        blog,
        company,
        html_url,
        avatar_url,
        location,
        followers,
        following,
        login,
        bio,
        hireable,
        public_repos,
        public_gists
        }= this.props.user;
        const {loading} = this.props;
        if(loading)
        return <Spinner />
        else
        return (
            <div className="single-user">
                <Link to='/' className="back btn btn-dark">
                    Back to Home
                </Link>
                <span className="hier btn btn-light">Hierable : {hireable?<i className="fas fa-check" style={{color:'green'}}></i>:<i className="fas fa-times-circle" style={{color:'red'}}></i>}</span>
                <div className="userProfile card my-2">
                    <div className="info">
                        <img src={avatar_url} className="prof-img" alt="userprofile"></img>
                        <h2>{name}</h2>
                        <p>Location : {location}</p>
                    </div>
                    <div className="bio">
                        {bio && <div><h3>Bio</h3><p>{bio}</p></div>}
                        <div>
                            <ul>
                                {name && <li>Username&nbsp;:&nbsp;{name}</li>}
                                
                                {company && <li>Company&nbsp;:&nbsp;{company}</li>}
                                
                                {blog && <li>Website&nbsp;:
                                    &nbsp;{blog}</li>}
                            </ul>
                        <a href={html_url} className="btn btn-dark my-2">Visit Github Profile</a>
                        </div>
                    </div>
                </div>
                <div className="S-2 card my-2">
                    <div className="all-center">
                        <div className="bdgs">
                            <div className="badges-primary">Followers : {followers}</div>
                            <div className="badges-success">Following : {following}</div>
                            <div className="badges-light">Public Repos : {public_repos}</div>
                            <div className="badges-dark">Public Gists : {public_gists}</div>
                        </div>
                    </div>
                    <Repos repos={this.props.repos} user={login}/>
                </div>
            </div>
        )
    }
}

export default User
