import React, { Component, Fragment } from 'react'

class ReposItems extends Component {
    render() {
        const {
            name,
            html_url,
            stargazers_count,
            watchers_count
        }=this.props.repo;
        return (
            <Fragment>
                <a href={html_url} target='blank'>
                    <div className="repositems">
                        <h3>{name}</h3>
                        <div>
                            <div className="stats badges-primary">{stargazers_count} <i className="fas fa-star"></i></div>
                            <div className="stats badges-dark">{watchers_count} <i className="fas fa-eye"></i></div>
                        </div>
                    </div>
                </a>
            </Fragment>
        )
    }
}

export default ReposItems
