import React, { Component } from 'react'

class Search extends Component {
    state = {
        text:''
    }
    onChange = (e)=> this.setState({ [e.target.name] : e.target.value})
    onSubmit = (e)=>{
        e.preventDefault();
        if(this.state.text!=='')
        {
            this.props.searchUsers(this.state.text);
            this.setState({text:''});
            e.target.children[0].value='';
        }
        else{
            this.props.showError('Please Enter Something','light');
        }
    }
    Clear = ()=> this.props.clearUI();
    render() {
        return (
            <div>
               <form className='srh-form' onSubmit={this.onSubmit}>
                   <input type='text' name='text' placeholder='Search Users...' onChange={this.onChange}></input>
                   <button type='submit' className='btn btn-dark'><i className='fas fa-search'></i>Submit</button>
                {this.props.showClear&& 
                <button className='btn clear' onClick={this.Clear}><i className='fas fa-times'></i>Clear</button>
                }
               </form> 
            </div>
        )
    }
}

export default Search
