import './App.css';
import Navbar from './components/layout/Navbar';
import React, { Fragment } from 'react';
import Users from './components/Users/Users';
import User from './components/Users/User';
import Alert from './components/layout/Alert';
import Search from './components/Search';
import About from './components/pages/About';
import {BrowserRouter as Router,Switch,Route} from 'react-router-dom';

class App extends React.Component{
  state = {
    users:[],
    user:{},
    loading:false,
    repos:[]
  }
  // Search Users
  searchUsers = async text=>{
    this.setState({users:[],loading:true});
    const res = await fetch(`https://api.github.com/search/users?q=${text}`);
    const users = await res.json();
    this.setState({users:users.items,loading:false});
  }
  // Clear UI
  ClearUI = () =>{
    this.setState({users:[],loading:false});
  }
  // Show Alert
  showAlert = (msg,type) =>{
    this.setState({alert:{msg,type}});
    setTimeout(()=>this.setState({alert:null}),3000);
  }
  // Get single user
  getUser = async username =>{
    this.setState({loading:true});
    const res = await fetch(`https://api.github.com/users/${username}`);
    const user = await res.json();
    this.setState({user,loading:false});
  }
  getRepos = async user => {
    const res = await fetch(`https://api.github.com/users/${user}/repos`);
    const repos = await res.json();
    this.setState({repos,loading:false})
  }
  render(){
  return (
    <Router>
    <div className="App">
      <Navbar />
      <div className='container'>
        <Alert alert={this.state.alert}/>
        <Switch>
          <Route exact path ='/' render={props=>(
            <Fragment>
              <Search searchUsers = {this.searchUsers} clearUI = {this.ClearUI} showClear = {this.state.users.length>0} showError = {this.showAlert}/>
              <Users users={this.state.users} loading={this.state.loading}/>  
            </Fragment>
          )
          }>
          </Route>
          <Route exact path='/about' component={About}></Route>
          <Route exact path='/user/:login' render={props=>(
            <User {...props} getUser={this.getUser} user={this.state.user} loading={this.state.loading} getRepos={this.getRepos} repos={this.state.repos}/>
          )}></Route>
        </Switch>
      </div>
    </div>
    </Router>
  );
  }
}

export default App;
